:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

#root,
body,
html {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.container {
  width: 375px;
  height: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
  background-size: 100% 100%;
}

#root {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

.box {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.selected {
  width: 100%;
  margin-top: 50px;
  padding-left: 30px;
}
.canvas {
  width: 300px;
  height: 300px;
}

.title {
  height: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 30px;
  font-size: 20px;
  font-family: "PingFang SC";
}

.btnGroup {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
}
/* From uiverse.io by @shah1345 */
.btn {
  display: inline-block;
  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  z-index: 1;
  color: #090909;
  padding: 0.2em 1.3em;
  font-size: 18px;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 6px 6px 12px #c5c5c5,
    -6px -6px 12px #ffffff;
}

.btn:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5,
    inset -4px -4px 12px #ffffff;
}

.btn:before {
  content: "";
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%) scaleY(1) scaleX(1.25);
          transform: translateX(-50%) scaleY(1) scaleX(1.25);
  top: 100%;
  width: 140%;
  height: 180%;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.btn:after {
  content: "";
  position: absolute;
  left: 55%;
  -webkit-transform: translateX(-50%) scaleY(1) scaleX(1.45);
          transform: translateX(-50%) scaleY(1) scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: #009087;
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.btn:hover {
  color: #ffffff;
  border: 1px solid #009087;
}

.btn:hover:before {
  top: -35%;
  background-color: #009087;
  -webkit-transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
          transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.btn:hover:after {
  top: -45%;
  background-color: #009087;
  -webkit-transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
          transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.addIcon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}
/* From uiverse.io by @EmmaxPlay */
.card {
    width: 330px;
    height: 500px;
    margin: 0 auto;
    background-color: #F8FBFE;
    border-radius: 8px;
    z-index: 1;
    position: absolute;
    top: 148px;
    left: 24px;
}

.cardTitle {
    display: flex;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
}
.titleText {
    margin-right: 25px;
}
.tools {
    display: flex;
    align-items: center;
}

.circle {
    padding: 0 4px;
}

.box1 {
    display: inline-block;
    align-items: center;
    width: 10px;
    height: 10px;
    padding: 1px;
    border-radius: 50%;
}

.red {
    background-color: #ff605c;
}

.yellow {
    background-color: #ffbd44;
}

.green {
    background-color: #00ca4e;
}


.right_x {
    display: inline-block;
    width: 18px;
    height: 3px;
    background: #999;
    line-height: 0;
    font-size: 0;
    vertical-align: middle;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    cursor: pointer;
}

.right_x:after {
    content: "/";
    display: block;
    width: 18px;
    height: 3px;
    background: #999;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.card__content {
    width: 100%;
    height: calc(100% - 44px);
    overflow-y: hidden;
    text-align: center;
}
.content_box {
    width: 100%;
    padding-top: 20px;
    height: calc(100% - 52px);
    overflow-y: auto;
}
.content_header {
    display: flex;
    justify-content: center;
}

/* 整个滚动条 */
::-webkit-scrollbar {
    /* 对应纵向滚动条的宽度 */
    width: 6px;
    /* 对应横向滚动条的宽度 */
    height: 10px;
}

/* 滚动条上的滚动滑块 */
::-webkit-scrollbar-thumb {
    background-color: rgba(144, 147, 153, 0.2);
    border-radius: 32px;
}

/* 滚动条轨道 */
::-webkit-scrollbar-track {
    background-color: rgba(144, 147, 153, 0.2);
    border-radius: 32px;
}

